.header {
    box-sizing: border-box;
    width: 100%;
    height: 80px;
    background-color: lightgrey;
    padding: 30px;
}

.header > div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}


.header a {
    color: rgb(103, 5, 5);
    font-size: large;
    text-decoration: none;
    font-size: x-large;
    font-weight: bold;
    margin-right: 10px;
}

.header>div>span {
    cursor: pointer;
    display: none;
    font-size: 20px;
}

.header>div>span:hover {
    color: grey;
}

  /* эти стили начнут срабатывать не устройствах меньше 700px */

.headerVert > div{
    display: none;
}

@media (max-width: 700px) {
    .header>div>span {
        display: block;
        font-size: 20px;
    }

    .header {
        background-color: lightblue;
        color: rgb(29, 8, 100);

        }
    a {
        text-decoration: none;
        margin: 10px;
    }

    .header a {
        display: none;
    }

    .headerVert{
        position: absolute;
        top: 80px; 
        right: 0;
        width: 25%;
        height: 30%;
        background-color: rgb(201, 238, 238);
        font-weight: bold;
    }

    .headerVert > div{
        display: flex;
        flex-direction: column;
    }
}

