.button {
    padding: 10px 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
    box-shadow: 4px 4px 8px #c2c2c2, -4px -4px 8px #ffffff;
    transition: box-shadow 0.6s ease-in-out, background-image 0.6s ease-in-out;
}

.button:hover {
    box-shadow: inset 4px 4px 8px #c2c2c2, inset -4px -4px 8px #ffffff;
}

.buttonDanger {
    background-image: linear-gradient(145deg, #ff4d4d, #ff0000);
    color: white;
}

.buttonDanger:hover {
    background-image: linear-gradient(145deg, #ff0000, #ff4d4d);
}

.buttonPrimary {
    background-image: linear-gradient(145deg, #4d79ff, #0033cc);
    color: white;
}

.buttonPrimary:hover {
    background-image: linear-gradient(145deg, #0033cc, #4d79ff);
}

.buttonActiv {
    background-color: rgb(85, 237, 71);
}

.buttonDisabled{
    background-color: #8a898a;
}
